.pyu-login-content {
  min-height: 100vh;
}


.form-container {
  width: 100%;
  // max-width: 300px;
  border-radius: 5px;
  border: 1px solid #eaeced;
  width: 250px;
  padding: 2em 2em;
}

.plugin-icon {
  display: block;
  margin-bottom: 0.5em;
  height: 80px;
  width: 80px;
}

.login-heading {
  margin-bottom: 1em;
}

// Small devices (landscape phones, 576px and up)
@media (min-width: 576px) {
  .form-container {
    width: 400px;
  }
  .plugin-icon {
    height: 100px;
    width: 100px;
  }
}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
  .form-container {
    width: 400px;
  }
  .plugin-icon {
    height: 100px;
    width: 100px;
  }
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
  .form-container {
    width: 400px;
  }
  .plugin-icon {
    height: 100px;
    width: 100px;
  }
}

// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
  .form-container {
    width: 450px;
  }
  .plugin-icon {
    height: 100px;
    width: 100px;
  }
}